import React from 'react'
import "../Css/Navibar.css"
import { Button, Row,Col  } from 'react-bootstrap';
import {BrowserRouter as Router,Link,useHistory} from "react-router-dom";
function Navibar() {
    const history = useHistory()
    return (
        <Row>
            <Col className="coolen justify-content-center"><Button className="button " variant="outline-primary" onClick={() =>history.push('/home')}>HOME</Button>{' '}
            <Button className="button " variant="outline-primary" onClick={() =>history.push('/services')}>SERVICES</Button>{' '}
            <Button className="button " variant="outline-primary" onClick={() =>history.push('/contact')}>CONTACT</Button>{' '}
            </Col>
            </Row>
       
    )
}

export default Navibar
